@import "_misc/smart-grid";
.items{
	@include row-float;
	@include wrapper;
	background: gray;
	@include clearfix;
}
.item{
	@include col-float;
		@include col-padding;
	
	outline: 1px solid #000;
	height: 100px;

	// &:nth-child(odd){
 //    	@include offset-left-md(1);
 //    	// @include offset-left-sm(0);
 //    	@include offset-left-xs(0);
    	
 //    }
}
.cards{

	.row{
		@include row-float;
		@include wrapper;
		
	}
}
.logotypes, .testimonials,
.news,
.footer,
.contacts{
	.row{
		@include clearfix;
	}
}
.cards-col, .news-col,
.features-col,
.contacts-col-4,
.contacts-col-2,
.logotypes-col,
.testimonials-col,
.footer-col{

	@include col-float;
		@include col-padding;
}

.testimonials-col{
	&:first-child{

		@include offset-left(3);
	}
	&.col-12{
		@include offset-left(0);
	}
}
.cards-col{

		&:nth-child(4n+1){
			clear: both;
		}
}

@media only screen and (max-width : 960px){
	.cards-col{


		&:nth-child(4n+1){
			clear: none;
		}

		&:nth-child(3n+1){
			clear: both;
		}
	}

	.logotypes-col{
		&:nth-child(4n+1){
			clear: both;
		}
	}
}

@media only screen and (max-width : 780px){
	.cards-col{


		// &:nth-child(4n+1){
		// 	clear: none;
		// }

		&:nth-child(3n+1){
			clear: none;
		}
		&:nth-child(2n+1){
			clear: both;
		}
	}

	.logotypes-col{
		&:nth-child(4n+1){
			clear: none;
		}
		&:nth-child(3n+1){
			clear: both;
		}
	}

	.testimonials-col{
		&:first-child{
			@include offset-left(0);
		}
	}
}

@media only screen and (max-width : 560px){
	.cards-col{

		@include offset-left-xs(1);
		// &:nth-child(4n+1){
		// 	clear: none;
		// }

		&:nth-child(3n+1){
			clear: none;
		}
		&:nth-child(2n+1){
			clear: both;
		}
	}
}